.slick-prev:before, .slick-next:before {
    font-size: 30px;
    color: grey;
}
.slick-slide > div {
    width: 75%;
    margin: auto;
    margin: 0 40px;
}
.slick-list {
    margin: 0 -10px;
}


@media all and (min-width: 200px) and (max-width: 320px)
{

    .hrClass{
        width: 100vh
    }

    .projects{
        width: 100vh;
    }

    .projectFlex{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .imageBox{
        width: 100vh;
        border-radius: 10px;
    }

}

@media all and (min-width: 320px) and (max-width: 700px)
{
    .hrClass{
        width: 100vh
    }

    .projects{
        width: 100vh;
    }

    .projectFlex{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .imageBox{
        height: 70vh;
    }

    .slider{
        width: 120%;
        margin: auto;
    }

}

@media all and (min-width: 700px) and (max-width: 1200px)
{
    .projectFlex{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .imageBox{
        width: 150vh;
        border-radius: 10px;
        height: 70vh;
    }

    .slider{
        width: 125%;
        margin: auto;
    }
}
  
  