@media all and (min-width: 200px) and (max-width: 320px)
{
    .hrClass{
        width: 100vh
    }

    .skills{
        width: 100vh;
        height: auto;
    }

    #skillGrid{
        grid-template-columns: repeat(2,1fr);
    }

}

@media all and (min-width: 320px) and (max-width: 700px)
{
    .hrClass{
        width: 100vh
    }

    .skills{
        width: 100vh;
        height: auto;
    }

    #skillGrid{
        grid-template-columns: repeat(3,1fr);
    }



}

@media all and (min-width: 700px) and (max-width: 1200px)
{
   
    #skillGrid{
        grid-template-columns: repeat(3,1fr);
    }
}
  
  