
   .resumeBtn {
    margin-top: 1rem;
    align-items: center;
    border-radius: 4em;
    font-weight: bold;
    color: black;
    background: 'orange';
    box-shadow: 0px 5px 18px rgba(136, 35, 35, 0.6);
  }

  .aboutme{
    height: 100vh;
  }

  .textAboutme{
    text-align: justify;
    font-size: 22px;
  }

  .textAboutmehead{
    font-size: 20px;
  }

  .textAboutmespan{
    font-size: 20px;
  }
  
  @media all and (min-width: 200px) and (max-width: 320px)
  {

    .hrClass{
      width: 100vh
    }

    .aboutme{
      width: 100vh;
      height: auto;
    }

    .aboutmeClass{
      flex-direction: column;
      /* justify-content: 'space-evenly'; */
      gap: 10px;
    }

    .textAboutme{
      /* text-align:center; */
      font-size: 35px;
    }

    .textAboutmehead{
      font-size: 45px;
    }

    .textAboutmespan{
      font-size: 30px;
    }

    .resumeBtn {
      width: 40%;
      font-weight: bold;
      font-size: 25px;
    }

    .homeimg{
      width: 100%;
      margin: auto;
    }

  }
  
  @media all and (min-width: 320px) and (max-width: 700px)
  {
    .hrClass{
      width: 100vh
    }

    .aboutme{
      width: 100vh;
      height: auto;
    }

    .aboutmeClass{
      flex-direction: column;
      /* justify-content: 'space-evenly'; */
      gap: 10px;
    }

    .textAboutme{
      /* text-align:center; */
      font-size: 25px;
    }

    .textAboutmehead{
      font-size: 30px;
    }

    .textAboutmespan{
      font-size: 25px;
    }


    .homeimg{
      margin: auto;
    }
  }

  @media all and (min-width: 700px) and (max-width: 1200px)
  {

    .aboutme{
      /* width: 100vh; */
      height: auto;
    }

    .textAboutme{
      /* text-align:center; */
      font-size: 20px;
    }

    .textAboutmehead{
      font-size: 20px;
    }

    .textAboutmespan{
      font-size: 20px;
    }


    .homeimg{
      width: 100%;
      margin: auto;
    }
  }
  
  